@import url('https://fonts.googleapis.com/css2?family=Alex+Brush&family=Bodoni+Moda&family=Courier+Prime:ital@1&family=Poppins:ital,wght@1,300&display=swap');

* {
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;

}

:root {
    --primary-color: #755139FF;
    --secondary-color: #D4B996FF;
}

body {
    background-color: var(--primary-color);
}

html {
    font-size: 75.5%;
}

.header {
    width: 100%;
    height: 400px;
    color: var(--secondary-color);
    box-shadow: 0 5px 10px var(--secondary-color);
    display: flex;
    align-items: center;
    position: relative;
}

.header::before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://images.pexels.com/photos/1662277/pexels-photo-1662277.jpeg?auto=compress&cs=tinysrgb&w=600");
    clip-path: circle(60% at left 300px);

    z-index: -1;
}

.row2 {
    position: absolute;
    top: 10px;
    right: 150px;
    text-align: center;
}

.row2 h2 {
    font-size: 3rem;
    margin-top: 20px;
    margin-bottom: 15px;
}

.row2 img {
    margin-top: 30px;
    width: 205px;
    display: flex;
}

.row1 h1 {
    font-size: 3rem;
    text-align: center;
    margin-top: 35px;
    font-family: 'Bodoni moda', serif;
}

.search {
    display: flex;
    align-items: center;
    justify-content: center;

}

.search input {
    outline: 0;
    border: 0;
    width: 350px;
    height: 35px;
    padding: 0 0.5rem;
    font-size: 15px;
    font-weight: bold;
}

.search button {
    outline: none;
    border: none;
    height: 35px;
    padding: 0 0.5rem;
}

.container {
    width: 94%;
    margin: 0 auto;
    margin-top: 25px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 0.5fr));
    grid-gap: 20px;
    box-sizing: border-box;
}

.card {
    background-color: var(--secondary-color);
    border-radius: 1rem;
    padding: 0.5rem;
    margin-bottom: 10px;
    box-shadow: 0 5px 5px rgba(212, 185, 150, 0.5);
    transition: .5s;
    text-align: center;
    position: relative;
    height: 100%;

}

.card:hover {
    transform: scale(0.9);
    background-color: rgba(255, 255, 255, 0.8);
}

.card img {
    width: 100%;
    height: 200px;
    border-radius: 1rem 1rem 0 0;

}

.card .bottom {
    display: flex;
    flex-direction: column;

}

.card .bottom .title {
    font-size: 14px;
    margin-bottom: 12px;


}

.card .bottom .amount {
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    font-size: 13px;
    font-weight: bold;
    padding: 0.2rem;
   
}

.overlay {
    min-height: 100hv;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.1));
    display: flex;
    align-items: center;

}

.overlay-inner {
    background: white;
    width: 700px;
    height: 550px;
    margin: auto;
    padding: 1.5rem;
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
    font-size: 1.3rem;

}

.overlay-inner .close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    outline: none;
    border: none;
    font-size: 17px;
    background-color: transparent;
}

.overlay-inner .inner-box {
    margin-top: 30px;
    display: flex;
    justify-content: center;
   

}

.overlay-inner .inner-box img {
    margin-right: 20px;
    width: 150px;
    height: 200px;
}

.overlay-inner .inner-box h3 {
    margin-top: 10px;
    color: green;
}

.overlay-inner .inner-box h4 {
    color: blue;
}

.overlay-inner .inner-box button {
    outline: none;
    border: none;
    width: 100px;
    border-radius: 5px;
    background-color: #24a0ed;
    font-weight: bold;
    padding: 0.3rem 0.5rem;
}

.overlay-inner .inner-box .description {
    margin-top: 2rem;
    text-align: justify;
    font-size: 13px;

}
@media only screen and (max-width:768px) { 
    .row2 img{
       position: absolute;
       left: 83px;
    }
    .row1 h1 {
        width: 264px;
    }
    .row2 {
        position: absolute;
        top: 11px;
        right: 17px;
        text-align: center;
    }
}
@media only screen and (max-width:320px) {
    .header {
       height: 521px;
    }

    .row1 h1{
       margin-right: 65px;
    }
.row2 img{
    display: none;
}
    .row2 {
        position: absolute;
        top: -13px;
        right: 8px;
        text-align: center;
        width: 298px;
    }


}